
import { defineComponent, ref, watch } from "vue";
import { useStore } from "vuex";
import {
  Actions,
  Exercise,
  ExerciseDifficulty,
  ExerciseTypes,
} from "@/store/types";
import "@/styles/Exercises.scss";

export default defineComponent({
  props: {
    isOpen: Boolean,
    exercise: {
      type: Object as () => Exercise | null,
      default: null,
    },
    formType: {
      type: String as () => "add" | "edit" | "details",
      required: true,
    },
  },
  emits: ["close", "submit"],

  setup(props, { emit }) {
    const store = useStore();
    const form = ref({
      name: props.exercise?.name || "",
      description: props.exercise?.description || "",
      type: props.exercise?.type || "",
      difficulty: props.exercise?.difficulty || "",
      duration: props.exercise?.duration || 0,
      repetitions: props.exercise?.repetitions || 0,
    });

    watch(
      () => props.exercise,
      (newExercise) => {
        if (newExercise) {
          form.value = {
            name: newExercise.name,
            description: newExercise.description,
            type: newExercise.type,
            difficulty: newExercise.difficulty,
            duration: newExercise.duration,
            repetitions: newExercise.repetitions || 0,
          };
        }
      },
      { immediate: true }
    );

    const handleSubmit = () => {
      if (props.formType === "add") {
        const newExercise: Exercise = {
          ...form.value,
          id: Date.now(),
        };
        store.dispatch("addExercise", newExercise);
      } else if (props.formType === "edit" && props.exercise) {
        const updatedExercise: Exercise = {
          ...props.exercise,
          ...form.value,
        };
        store.dispatch("updateExercise", updatedExercise);
      }
      closeModal();
    };

    const closeModal = () => {
      emit("close");
    };

    return {
      form,
      handleSubmit,
      closeModal,
      actions: Actions,
      exerciseTypes: ExerciseTypes,
      exerciseDifficulty: ExerciseDifficulty,
    };
  },
});
